@import "browser_icons";

#content > div:first-child {
	display: none;

	//noinspection CssOptimizeSimilarProperties
	@include if-browser-has-icon {
		display: block;
		width: 128px;
		height: 128px;
		background-repeat: no-repeat;
		background-position: center;
		margin: 0 auto;
	}
	@include browser-backgrounds();
}
